(function() {
  'use strict';

  angular
    .module('client')
    .service('user', user);

  /** @ngInject */
  function user() {
      var data = {
		  avatar: "no-userphoto.gif",
          loveName: "",
          username: '',
          password: '',
          status: 0,     // 0 未登录， 1 已登录
          remFlag: false,
          userId: ""
      }

      if(window.localStorage) {
          data.username = localStorage.username;
          data.password = localStorage.password;
          data.remFlag = localStorage.remFlag;
      }
      console.log("=++===++=+=+");
      console.log(data.loveName && data.username);
      data.name = data.loveName ? data.loveName : data.username;
      console.log(localStorage);
      return {
          getData: function() {
              if(window.localStorage) {
                  data.username = localStorage.username;
                  data.password = localStorage.password;
                  data.remFlag = localStorage.remFlag;
              }
              return data;
          },
          setUser: function(username, password, remFlag, userData) {
              console.log(username);
              console.log(password);
              localStorage.username = username;
              localStorage.remFlag = remFlag;
              data.username = username;
              data.remFlag = remFlag;
              data.userId = userData.userId;
              data.avatar = userData.avatar ? userData.avatar : "no-userphoto.gif";
              data.name = userData.loveName ? userData.loveName : userData.username;
              if (remFlag) {
                  localStorage.password = password;
                  data.password = password;
              }
              else {
                  data.password = "";
                  localStorage.password = "";
              }
          },
          setStatu: function(status) {
              data.status = status;
          },
          getStatus: function() {
              return data.status;
          },
          clearUser: function() {
              data = {
        		  avatar: "no-userphoto.gif",
                  loveName: "",
                  username: '',
                  password: '',
                  status: 0,     // 0 未登录， 1 已登录
                  remFlag: false,
                  userId: ""
              };
          },
          getUserId: function() {
              return data.userId;
          },
          getUsername: function() {
              return data.username;
          },
          autoSetUser: function(username, password) {
              data.username = username;
              data.password = password;
          }
      }
  }

})();

(function() {
  'use strict';

  angular
    .module('client')
    .service('pattern', pattern);

  /** @ngInject */
  function pattern() {
    return {
      reg: {
        username: /^(13[0-9]|15[0|1|3|6|7|8|9]|18[8|9])\d{8}$|^(?:[\w\!\#\$\%\&\'\*\+\-\/\=\?\^\`\{\|\}\~]+\.)*[\w\!\#\$\%\&\'\*\+\-\/\=\?\^\`\{\|\}\~]+@(?:(?:(?:[a-zA-Z0-9](?:[a-zA-Z0-9\-](?!\.)){0,61}[a-zA-Z0-9]?\.)+[a-zA-Z0-9](?:[a-zA-Z0-9\-](?!$)){0,61}[a-zA-Z0-9]?)|(?:\[(?:(?:[01]?\d{1,2}|2[0-4]\d|25[0-5])\.){3}(?:[01]?\d{1,2}|2[0-4]\d|25[0-5])\]))$/,
        password: /^[a-zA-Z\W0-9]{6,20}$/,
        name: /^([\u4e00-\u9fa5]){2,7}$/,    // 人名
        phone: /^(13[0-9]|15[0|1|3|6|7|8|9]|18[8|9|5])\d{8}$/,  // 手机号
        tel: /0\d{2,3}-\d{5,9}|0\d{2,3}-\d{5,9}/
      },
      msg: {
        username: '请输入11位手机号或者邮箱',
        password: '6-20位字符，可由英文、数字及标点符号组成',
        repeatPassword: '密码不一致',
        name: '请输入2-7位中文字符',
        captcha: '验证码错误',
        phone: '手机号格式错误',
        tel: '固定电话格式错误',
        require: '必填'
      }
    };
  }

})();

