(function() {
  'use strict';

  angular
    .module('client')
    .directive('carouselBox', carouselBox);

  /** @ngInject */
  function carouselBox($compile) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/carouselBox/carouselBox.tpl.html',
      controller: CarouselBoxController,
      link: CarouselBoxLink
    };

    return directive;

    /** @ngInject */
    function CarouselBoxController($scope) {
        $scope.imgUrls = $scope.oldHouseMsg.imgUrls;
    }

    function CarouselBoxLink(scope, elem, attrs, ctrl) {
        var new_li;
        console.log(scope.imgUrls);
        for(var i = 0, len = scope.imgUrls.length; i < len; i++) {
            new_li = $("<li></li>");

            new_li.html("<img src='assets/images/oldHouse/" + scope.imgUrls[i] + "' width='130' enlarge-pic height='98'><div class='bun_bg'></div>");
            $(".small_list ul").append($compile(new_li)(scope));
        }

        for(var i = 0, len = scope.imgUrls.length; i < len; i++) {
            new_li = $("<li></li>");
            new_li.html("<img src='assets/images/oldHouse/" + scope.imgUrls[i] + "'  width='690' enlarge-pic height='517'>");
            $(".large_box ul").append($compile(new_li)(scope));
        }
        (function($){
            //默认参数
            var defaluts = {
                large_elem: "large_elem",     //大图
                small_elem: "small_elem",    //小图
                left_btn: "left_btn",        //左按钮
                right_btn: "right_btn" ,    //右按钮
                state: "on",                 // 类
                speed: "normal",             //速度
                vis: 4                        //项数
            };
            $.fn.extend({
                /* 带缩略图的轮播效果 */
                "thumbnailImg": function (options) {
                    var opts = $.extend({}, defaluts, options); //使用jQuery.extend 覆盖插件默认参数
                    //遍历匹配元素的集合
                    return this.each(function () {
                        var $this = $(this);
                        /* 初始化 */
                        $(opts.large_elem).find("ul li").eq(0).show();
                        $(opts.small_elem).find("ul li").eq(0).addClass(opts.state);
                        var l = $(opts.small_elem).find("ul li").length;
                        var l_mean;
                        if(l < opts.vis){
                            l_mean = 0;
                        }else{
                            l_mean = ((parseInt(l / opts.vis) - 1) * opts.vis) + (l % opts.vis);
                        }
                        var w = $(opts.small_elem).find("ul li").outerWidth(true);  //缩略图项的宽度（包含内边距）
                        $(opts.small_elem).find("ul").css("width",l * w + "px");    //初始化缩略图总宽度
                        /* 缩略图点击 */
                        $(opts.small_elem).find("ul li").click(function(){
                            $(this).addClass(opts.state).siblings().removeClass(opts.state);
                            Img($(this).index());
                        });
                        /* 左按钮 */
                        $(opts.left_btn).click(function(){
                            var i;
                            $(opts.small_elem).find("ul li").each(function(index){
                                if($(this).hasClass(opts.state)){
                                    i = index;
                                }
                            });
                            i--;
                            if(i < 0){
                                i = l - 1;
                            }
                            $(opts.small_elem).find("ul li").eq(i).addClass(opts.state).siblings().removeClass(opts.state);
                            var ml = i * w;
                            if(ml <= l_mean * w){
                                $(opts.small_elem).find("ul").stop().animate({
                                    marginLeft: -ml + "px"
                                },opts.speed)
                            }else{
                                $(opts.small_elem).find("ul").stop().animate({
                                    marginLeft: -(l_mean * w) + "px"
                                },opts.speed)
                            }
                            Img(i)
                        });
                        /* 右按钮 */
                        $(opts.right_btn).click(function(){
                            var i;
                            $(opts.small_elem).find("ul li").each(function(index){
                                if($(this).hasClass(opts.state)){
                                    i = index;
                                }
                            });
                            i++;
                            if(i > l-1){
                                i = 0;
                            }
                            $(opts.small_elem).find("ul li").eq(i).addClass(opts.state).siblings().removeClass(opts.state);
                            var ml = i * w;
                            if(ml <= l_mean * w){
                                $(opts.small_elem).find("ul").stop().animate({
                                    marginLeft: -ml + "px"
                                },opts.speed)
                            }else{
                                $(opts.small_elem).find("ul").stop().animate({
                                    marginLeft: -(l_mean * w) + "px"
                                },opts.speed)
                            }
                            Img(i);
                        });
                        /* 大图 */
                        function Img(i){
                            $(opts.large_elem).find("ul li").eq(i).fadeIn().siblings().hide();
                        }
                    });
                }
            });
        })(jQuery);

        $(function() {
            /* 商品轮播图（带缩略图的轮播效果） */
            $(".banner").thumbnailImg({
                large_elem: ".large_box",
                small_elem: ".small_list",
                left_btn: ".left_btn",
                right_btn: ".right_btn"
            });

        });
    }
  }

})();

(function() {
  'use strict';
  console.log("addressSelector1");
  angular
    .module('client')
    .directive('addressSelector', addressSelector);

  /** @ngInject */
  function addressSelector($http, $q, $compile) {
      console.log("addressSelector");
      var cityURL, delay, templateURL;
      delay = $q.defer();
      templateURL = 'scripts/template/index.html';
      cityURL = 'scripts/template/city.min.js';
      $http.get(cityURL).success(function(data) {
        return delay.resolve(data);
      });
      return {
        restrict: 'AE',
        scope: {
          p: '=',
          a: '=',
          c: '=',
          d: '=',
          ngModel: '='
        },
        link: function(scope, element, attrs) {
          var popup;
          popup = {
            element: null,
            backdrop: null,
            show: function() {
              return this.element.addClass('active');
            },
            hide: function() {
              this.element.removeClass('active');
              return false;
            },
            resize: function() {
              if (!this.element) {
                return;
              }
              this.element.css({
                top: -this.element.height() - 30,
                'margin-left': -this.element.width() / 2
              });
              return false;
            },
            focus: function() {
              $('[ng-model="d"]').focus();
              return false;
            },
            init: function() {
              element.on('click keydown', function() {
                popup.show();
                event.stopPropagation();
                return false;
              });
              $(document).bind("click",
              function(e) {
                  var target = $(e.target);
                  if (target.closest(".select-address").length == 0) {
                      popup.hide();
                  }
              });
              this.element.on('click', function() {
                return event.stopPropagation();
              });
              return setTimeout((function(_this) {
                return function() {
                  _this.element.show();
                  return _this.resize();
                };
              })(this), 500);
            }
          };
          return delay.promise.then(function(data) {
            $http.get(templateURL).success(function(template) {
              var $template;
              $template = $compile(template)(scope);
              $('body').append($template);
              popup.element = $($template[2]);
              scope.provinces = data;
              return popup.init();
            });
            scope.aSet = {
              p: function(p) {
                scope.p = p;
                scope.c = null;
                scope.a = null;
                return scope.d = null;
              },
              c: function(c) {
                scope.c = c;
                scope.a = null;
                return scope.d = null;
              },
              a: function(a) {
                scope.a = a;
                scope.d = null;
                return popup.focus();
              }
            };
            scope.clear = function() {
              scope.p = null;
              scope.c = null;
              scope.a = null;
              return scope.d = null;
            };
            scope.submit = function() {
              return popup.hide();
            };
            scope.$watch('p', function(newV) {
              var v, _i, _len, _results;
              if (newV) {
                _results = [];
                for (_i = 0, _len = data.length; _i < _len; _i++) {
                  v = data[_i];
                  if (v.p === newV) {
                    _results.push(scope.cities = v.c);
                  }
                }
                return _results;
              } else {
                return scope.cities = [];
              }
            });
            scope.$watch('c', function(newV) {
              var v, _i, _len, _ref, _results;
              if (newV) {
                _ref = scope.cities;
                _results = [];
                for (_i = 0, _len = _ref.length; _i < _len; _i++) {
                  v = _ref[_i];
                  if (v.n === newV) {
                    _results.push(scope.dists = v.a);
                  }
                }
                return _results;
              } else {
                return scope.dists = [];
              }
            });
            return scope.$watch(function() {
              scope.ngModel = '';
              if (scope.p) {
                scope.ngModel += scope.p;
              }
              if (scope.c) {
                scope.ngModel += " " + scope.c;
              }
              if (scope.a) {
                scope.ngModel += " " + scope.a;
              }
              if (scope.d) {
                scope.ngModel += " " + scope.d;
              }
              return popup.resize();
            });
          });
        }
      };
  }

}).call(this);

